import axios from '../axios'
/* 
 * 
 */
// jb
export const findJbAll = () => {
    return axios({
        url: '/regAmount/findJbAll',
        method: 'get',
    })
}

// 
export const findCxAll = () => {
    return axios({
        url: '/regAmount/findCxAll',
        method: 'get',
    })
}



// 
export const batchDelete = (data) => {
    return axios({
        url: '/regAmount/batchDelete',
        method: 'post',
        data
    })
}

// 
export const save = (data) => {
    return axios({
        url: '/regAmount/save',
        method: 'post',
        data
    })
}






